.footer {
    background-color: #101d2c;
    padding: 8rem;

    &-nav {
        list-style: none;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));
        grid-gap: 2rem;
        align-items: center;

        &--link {
            &:link, &:visited {
                font-size: 1.4rem;
                color: #fff;
                text-decoration: none;
                text-transform: uppercase;
                text-align: center;
                padding: 1.5rem;
                display: block;
                transition: all .2s;
            }

            &:hover, &:active {
                background-color: rgba(255,255,255, 0.05);
                transform: translateY(-3px);
            }
        }
    }

    .copyright {
        font-size: 1.4rem;
        color: #aaa;
        margin-top: 6rem;
        margin-right: auto;
        margin-left: auto;
        text-align: center;
        width: 70%;
    }
}